.footer
    background: $white
    &__content
        padding: 38px 0 63px 0
        display: grid
        grid-template-columns: repeat(3, 1fr)
    &__links
        width: 558px
        margin-right: 154px
    &__link
        display: grid
        grid-template-columns: repeat(2, 1fr)
        &:nth-child(1)
            margin-bottom: 40px
        &-item
            display: flex
            margin-bottom: 10px
    &__phone
        text-align: right
        display: flex
        flex-direction: column
        align-items: flex-end
        &-link
            display: inline-block
            font-weight: 500
            font-size: 27px
            margin-bottom: 20px
        &-text
            position: relative
            width: 153px
            margin-bottom: 20px
            &::after
                position: absolute
                content: ''
                left: 0
                top: 3px
                background: url('../img/footer/phone.svg')
                width: 14px
                height: 14px
        &-social
            width: 78px
            display: flex
            justify-content: space-between

@media screen and (max-width:1280px)
    .footer
        &__links
            margin-right: 0
        &__logo
            width: 123px
            margin-right: 30px
        &__phone
            position: absolute
            right: 0
            top: 30px

@media screen and (max-width:961px)
    .footer
        &__links
            width: 435px
        &__phone
            &-link
                font-size: 22px
@media screen and (max-width: 790px)
    .footer
        &__content
            display: grid
            grid-template-columns: 1fr
        &__links
            width: 100%
            margin-top: 153px
        &__contact
            display: grid
            grid-template-columns: 1fr
            gap: 20px
        &__phone
            top: 95px
            left: 0
            align-items: start
