.price
    &__description
        font-weight: 400
        font-size: 18px
        color: #363348
        padding-top: 20px
        padding-bottom: 40px
        width: 770px
    &__arrow
        width: 52.5px
        height: 52.5px
        background: $white
        box-shadow: 0px 12px 14px rgba(54, 51, 72, 0.137812)
        border-radius: 50px
        position: absolute
        z-index: 9
        top: 50%
        transform: translateY(-50%)
        cursor: pointer
        &:after
            position: absolute
            content: ''
            background: url('../img/arrow.png') no-repeat
            background-position: center center
            width: 52.5px
            height: 52.5px
            top: 0
            left: 0
            z-index: 9
        &-right
            right: -2%
            &:after
                transform: rotate(90deg)
        &-left
            left: -2%
            &:after
                transform: rotate(-90deg)
    &__block
        &-item
            padding: 44px 45px 43px 45px
            background: #FFFFFF
            box-shadow: 0px 4px 42px rgba(0, 0, 0, 0.04)
            border-radius: 24px
            position: relative
            margin-right: 20px
            margin: 37px 10px
            &__title
                font-size: 25px
                line-height: 32px
                color: $gray
                font-weight: 400
            &__adress
                margin: 20px 0 10px 0
                font-weight: 700
                line-height: 26px
            &__button
                width: 223px
                height: 55px
                margin: 0 auto
                margin-top: 49px
        &-data
            display: flex
            justify-content: space-between
            align-items: center
            margin-bottom: 5px
            height: 40px
            &__long
                justify-content: flex-start
                .price__block-data__label
                    margin-left: 43px
            &__time
                font-weight: 700
                color: $gray
            &__cell
                font-weight: 400
                font-size: 33px
                text-transform: uppercase
                color: $gray
            &__label
                padding: 0 20px
                background: #BFBFBF
                border-radius: 4px
                font-weight: 700
                color: #FFFFFF
            &__clue
                position: absolute
                bottom: 108px

@media screen and (max-width:961px)
    .price
        &__description
            width: 582px
        &__arrow
            &-right
                right: 0
            &-left
                left: 0

@media screen and (max-width:790px)
    .price
        &__block
            &-item
                padding: 24px 20px 23px 20px
                margin: 27px 10px

        &__description
            width: 100%
            font-size: 14px

@media screen and (max-width:372px)
    .price
        &__arrow
            top: -10px
            &-right
                right: 0
            &-left
                left: 60%
        .content
            max-width: 100%
        &__block
            &-item
                margin: 27px 10px
                &__title
                    font-size: 18px
                &__button
                    width: 95%
            &-data
                &__cell
                    font-size: 24px
                &__label
                    padding: 0 14px
                &__long
                    .price__block-data__label
                        margin-left: 13px
