}
.header:after {
  position: absolute;
  content: "";
  top: 79px;
  left: 0;
  width: 100%;
  height: 1px;
  background: #DDDFE0;
}
.header__phone a {
  font-weight: 500;
  font-size: 22px;
  line-height: 26px;
  letter-spacing: -1.01538px;
  color: #000000;
}

#header {
  background: #FFFFFF;
  box-shadow: 0px 0px 34px rgba(0, 0, 0, 0.12);
  border-radius: 0px;
}

.navigation {
  display: flex;
  width: 639px;
  justify-content: space-between;
}
.navigation__mobile {
  display: none;
}
.navigation:nth-child(1) {
  padding-bottom: 27px;
  padding-top: 7px;
}
.navigation:nth-child(2) {
  padding-bottom: 29px;
  margin-top: 13px;
}

@media screen and (max-width: 1280px) {
  .navigation {
    width: 533px;
  }
}
@media screen and (max-width: 961px) {
  .navigation {
    width: 433px;
  }
}
@media screen and (max-width: 790px) {
  #header {
    box-shadow: 0px 0px 34px rgba(0, 0, 0, 0);
  }
  .header {
    padding: 15px 0;
    border-bottom: none;
    background: transparent;
    box-shadow: 0px 0px 34px rgba(0, 0, 0, 0);
  }
  .header:after {
    background: transparent;
  }
  .header__logo {
    z-index: 99;
    margin-left: 45px;
  }
  .header__logo img {
    width: 84px;
  }
  .header__phone {
    display: none;
  }
  .header__burger {
    z-index: 99;
    cursor: pointer;
    position: absolute;
    top: 22px;
    left: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 21px;
    height: 13px;
  }
  .header__burger-line {
    display: block;
    width: 21px;
    height: 1.17px;
    background: #000000;
    z-index: 99;
  }
  .navigation {
    display: none;
  }
  .navigation__mobile {
    display: flex;
    position: absolute;
    width: 90%;
    height: 100vh;
    background: #F9F9F9;
    top: 0;
    left: -600px;
    z-index: 90;
    transition: 0.3s ease-in-out;
  }
  .navigation__mobile.active {
    display: flex;
    left: 0;
  }
  .navigation__mobile-close {
    right: 21px;
    top: 30px;
    position: absolute;
    cursor: pointer;
  }
  .navigation__mobile ul {
    padding-top: 121px;
    padding-left: 34px;
    display: flex;
    flex-direction: column;
    font-size: 20px;
  }
  .navigation__mobile-item {
    padding-bottom: 15px;
  }
  .navigation__mobile-link {
    color: #757070;
  }
}
