.overflow {
  opacity: 0;
  height: 0;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
  background: rgba(217, 217, 217, 0.35);
  backdrop-filter: blur(8px);
  transition: opacity 0.3s ease-in-out;
}
