.reviews
    position: relative
    &__slider
        max-width: 100%
        margin-top: 40px
        &-item__img
            width: 90%
@media screen and (max-width:376px)
    .reviews__slider
        padding-top: 30px
        &-item__img
            width: 90%
@media screen and (max-width:336px)
    .reviews__slider
        padding-top: 30px
        &-item__img
            width: 90%
        .price__arrow-left
            left: 52%
