.header
    display: flex
    align-items: start
    justify-content: space-between
    border-bottom: 1px solid #DDDFE0
    padding-top: 27px
    position: relative
    &:after
        position: absolute
        content: ''
        top: 79px
        left: 0
        width: 100%
        height: 1px
        background: #DDDFE0
    &__phone
        a
            font-weight: 500
            font-size: 22px
            line-height: 26px
            letter-spacing: -1.01538px
            color: $black
#header
    background: #FFFFFF
    box-shadow: 0px 0px 34px rgba(0, 0, 0, 0.12)
    border-radius: 0px
.navigation
    display: flex
    width: 639px
    justify-content: space-between
    &__mobile
        display: none
    &:nth-child(1)
        padding-bottom: 27px
        padding-top: 7px
    &:nth-child(2)
        padding-bottom: 29px
        margin-top: 13px
@media screen and (max-width:1280px)
    .navigation
        width: 533px
@media screen and (max-width:961px)
    .navigation
        width: 433px

@media screen and (max-width:790px)
    #header
        box-shadow: 0px 0px 34px rgba(0, 0, 0, 0)
    .header
        padding: 15px 0
        border-bottom: none
        background: transparent
        box-shadow: 0px 0px 34px rgba(0, 0, 0, 0)
        &:after
            background: transparent
        &__logo
            z-index: 99
            margin-left: 45px
            img
                width: 84px
        &__phone
            display: none
        &__burger
            z-index: 99
            cursor: pointer
            position: absolute
            top: 22px
            left: 10px
            display: flex
            flex-direction: column
            justify-content: space-between
            width: 21px
            height: 13px
            &-line
                display: block
                width: 21px
                height: 1.17px
                background: #000000
                z-index: 99
    .navigation
        display: none
        &__mobile
            display: flex
            position: absolute
            width: 90%
            height: 100vh
            background: $white
            top: 0
            left: -600px
            z-index: 90
            transition: 0.3s ease-in-out
            &.active
                display: flex
                left: 0
            &-close
                right: 21px
                top: 30px
                position: absolute
                cursor: pointer
            ul
                padding-top: 121px
                padding-left: 34px
                display: flex
                flex-direction: column
                font-size: 20px
            &-item
                padding-bottom: 15px
            &-link
                color: #757070

@media screen and (max-width:380px)
    #header
        .content
            max-width: 100%
